* {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

body {
    background: faf5f6;
    font-family: "Quicksand", sans-serif;
    color: #333;
}

aside.context {
    text-align: center;
    color: #333;
    line-height: 1.7;
}

aside.context a {
    text-decoration: none;
    color: #333;
    padding: 3px 0;
    border-bottom: 1px dashed;
}

aside.context .explanation {
    max-width: 700px;
    margin: 4em auto 0;
}

/* h1 {
    width: 100%;
    margin: 3em auto 1.5em;
    font-size: 40px;
    text-align: center;
} */

h1 small {
    text-transform: none;
    display: block;
    margin: 20px 0;
    font: 300 20px "QuickSand", sans-serif;
    color: #999;
}

h4 {
    width: 100%;
    margin: 2em auto 0;
    font-size: 20px;
    text-align: center;
}

h4 small {
    text-transform: uppercase;
    display: block;
    margin: 10px 0;
    font: 300 15px "QuickSand", sans-serif;
    color: #999;
}

.main-content {
    display: flex;
    flex-wrap: wrap;
    margin: 3em auto 0;
    max-width: 1000px;
    justify-content: space-around;
    align-items: center;
}

.palette__wrapper {
    flex: 1;
    padding: 20px;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.palette {
    width: 290px;
    height: 210px;
    position: relative;
    display: inline-block;
    perspective: 800px;
}

.palette__cover {
    border-radius: 6px;
    width: inherit;
    height: inherit;
    position: absolute;
    top: 0;
    z-index: 2;
    transition: 0.3s ease;
    transform-origin: top left;
    font: 900 28px/0.9 "Poppins", sans-serif;
    text-transform: uppercase;
    color: #fff;
    overflow: hidden;
}

.palette__cover__border {
    width: 80%;
    height: 80%;
    border: 7px solid #fff;
    margin: 10%;
}

.palette__cover span {
    display: inline-block;
    position: absolute;
    bottom: 7.5px;
    left: 10px;
    padding: 10px;
    max-width: 50%;
}

.palette__cover__top {
    position: absolute;
    left: 0;
    width: calc(100% + 64px);
    margin-left: -32px;
    z-index: 4;
    border-radius: 3px 3px 4px 4px;
    opacity: 0;
}

.palette__base {
    background: #3d334d;
    /* background: #ddc8ff; */
    width: inherit;
    height: inherit;
    position: absolute;
    top: 0;
    border-radius: 9px;
    padding: 15px;
    grid-gap: 8px;
    border: #0000000d 5px solid;
}

.palette:hover .palette__cover {
    transform: rotateX(78deg);
}

.palette:hover .palette__cover__top {
    animation: startTransition 0.17s forwards 0.1s;
}

@keyframes startTransition {
    0% {
        opacity: 0;
        top: 200px;
        transform: scale(0.5);
        height: 0;
    }
    40% {
        opacity: 1;
    }
    100% {
        top: 22px;
        opacity: 1;
        transform: scale(1);
        height: 15px;
        box-shadow: 0 5px 10px -2px rgba(0, 0, 0, 0.2);
    }
}

.palette__shade {
    border-radius: 4px;
    box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.9);
}

.palette--one .palette__base {
    padding: 70px 65px;
}

.palette--one .palette__cover {
    background: #ffc303;
}

.palette--one .palette__cover__top {
    background: #ecb404;
}

.palette--one .palette__cover span {
    max-width: 50%;
    background: #ffc303;
}

.palette--two .palette__base {
    padding: 90px 50px;
}

.palette--two .palette__cover {
    background: #ee4266;
}

.palette--two .palette__cover__top {
    background: #e23a5d;
}

.palette--two .palette__cover span {
    max-width: 62%;
    background: #ee4266;
}

.palette--three .palette__cover {
    background: #0075c4;
}

.palette--three .palette__cover__top {
    background: #016cb4;
}

.palette--three .palette__cover span {
    max-width: 48%;
    background: #0075c4;
}

.palette--three .palette__base {
    padding: 50px 70px;
}

.palette--four .palette__base {
    padding: 46px 86px;
}

.palette--four .palette__cover {
    background: #2a2e45;
}

.palette--four .palette__cover__top {
    background: #171926;
}

.palette--four .palette__cover span {
    max-width: 59%;
    background: #2a2e45;
}